@use "../utils/_functions" as *;

.section {

	&--default {
		margin: 0 0 get-rem(50);

		@media(min-width: 1200px) {
			margin: 0 0 get-rem(72);
		}
	}

	&--tours {
		margin: 0 0 get-rem(50);

		@media(min-width: 1200px) {
			margin: 0 0 get-rem(82);
		}
	}

	&--quest-form {
		padding: get-rem(30) 0 0;

		@media(min-width: 768px) {
			padding: get-rem(45) 0 0;
		}
	}

	&--services-list {
		margin: get-rem(60) 0 get-rem(40);

		@media(min-width: 1200px) {
			margin: get-vw(188) 0 get-vw(79);
		}

		@media(min-width: 1921px) {
			margin: get-rem(188) 0 get-rem(79);
		}
	}

	&--rooms-card {
		margin: get-rem(56) 0;

		@media(min-width: 768px) {
			.title-sec {
				margin: 0 auto get-rem(79);
			}
		}
	}

	&--options-block {
		position: relative;
		z-index: 4;
	}
}
