@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.title-block {
	margin: 0 0 get-rem(18);

	@media(min-width: 768px) {
		margin: 0 0 get-rem(25);
	}
}

.title {
	margin: 0;

	&--small {
		font-size: get-rem(24);
	}

	&--large {
		font-family: $secFont;
		font-size: 19vw;
		text-transform: uppercase;
		font-weight: 500;
		line-height: 1.1;

		@media(min-width: 560px) {
			font-size: get-rem(100);
		}

		@media(min-width: 768px) {
			font-size: get-rem(120);
		}

		@media(min-width: 1280px) {
			font-size: get-vw(160);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(160);
		}
	}

	&--middle {
		font-family: $secFont;
		font-size: get-rem(24);
		line-height: 1.4;

		@media(min-width: 560px) {
			font-size: get-rem(32);
		}

		@media(min-width: 768px) {
			font-size: get-rem(40);
		}

		@media(min-width: 1280px) {
			font-size: get-rem(48);
		}
	}

	&--section {
		font-size: get-rem(36);
		text-transform: uppercase;
		font-weight: 500;

		@media(min-width: 560px) {
			font-size: get-rem(40);
		}

		@media(min-width: 768px) {
			font-size: get-rem(48);
		}

		@media(min-width: 1280px) {
			font-size: get-rem(54);
		}
	}

	&--section-sec {
		font-family: $secFont;
		font-size: get-rem(36);
		font-weight: 500;

		@media(min-width: 560px) {
			font-size: get-rem(40);
		}

		@media(min-width: 768px) {
			font-size: get-rem(54);
		}

		@media(min-width: 1200px) {
			font-size: get-vw(64);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(64);
		}
	}

	&--white {
		color: var(--color-white);
	}

	&--bold {
		font-weight: bold;
	}

	&--ttu {
		text-transform: uppercase;
	}
}

.gold-text-gradient {
	background: linear-gradient(to bottom, var(--color-gold), var(--color-white));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	display: inline-block;
}


.simple-text {
	font-family: $secFont;
	line-height: 1.4;
	font-size: get-rem(24);
	color: var(--color-white);

	&--small {
		font-size: get-rem(24);

		@media(min-width: 992px) {
			font-size: get-vw(32);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(32);
		}
	}

	&--mid {

		.profile-item & {
			font-size: get-rem(20);
		}

		@media(min-width: 992px) {
			font-size: get-rem(32);

			.profile-item & {
				font-size: get-vw(40);
			}
		}

		@media(min-width: 992px) {
			font-size: get-vw(40);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(40);
		}
	}

	&--white {
		color: var(--color-white);
	}

	&--gray {
		color: rgba(#fff, 0.5);
	}

	&--gray-light {
		color: rgba(#fff, 0.8);
	}

	p {
		margin: 0;

		+ p {
			margin: get-rem(20) 0 0;

			@media(min-width: 992px) {
				margin: get-vw(60) 0 0;
			}

			@media(min-width: 1921px) {
				margin: get-rem(60) 0 0;
			}
		}
	}
}


.default-ul {
	padding: 0;
	margin: 0;

	li {
		font-family: $secFont;
		line-height: 1.4;
		font-size: get-rem(24);
		color: var(--color-white);
		display: flex;
		align-items: center;
		gap: get-rem(16);

		@media(min-width: 768px) {
			font-size: get-rem(32);
			gap: get-rem(28);
		}

		@media(min-width: 992px) {
			font-size: get-vw(40);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(40);
		}

		&::before {
			content: '';
			display: block;
			width: get-rem(4);
			height: get-rem(4);
			background-color: #fff;
			border-radius: 100%;
		}
	}

	&--sec {
		li:before {
			display: none;
		}
	}

	&--sm {

		li {
			text-transform: capitalize;
			font-size: get-rem(24);

			@media(min-width: 992px) {
				font-size: get-vw(32);
			}

			@media(min-width: 1921px) {
				font-size: get-rem(32);
			}
		}
	}
}
