@use "../utils/_functions" as *;

.footer {
	padding: get-rem(50) 0 get-rem(30);

	@media(min-width: 560px) {
		padding: get-rem(50) 0;
	}

	@media(min-width: 1360px) {
		padding: get-vw(92) 0;
	}

	@media(min-width: 1921px) {
		padding: get-rem(92) 0;
	}

	&__body {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: get-rem(16);

		@media(min-width: 560px) {
			gap: get-rem(12);
		}

		@media(min-width: 992px) {
		    flex-wrap: nowrap;
		}

		@media(min-width: 1360px) {
			gap: get-vw(16);
		}

		@media(min-width: 1921px) {
			gap: get-rem(16);
		}

		@media(max-width: 991px) {
		    .subscribe-form {
			    width: 48%;
		    }

			.footer__links-sec {
				width: 24%;
			}

			.footer__right {
				width: 24%;
			}
		}

		@media(max-width: 660px) {

			.subscribe-form {
				width: 100%;
				margin: 0 0 get-rem(20);
			}

			.footer__links-sec {
				width: 48%;
				text-align: center;

				img {
					margin: 0 auto;
				}
			}

			.footer__right {
				width: 48%;

				.link-icon {
					justify-content: center;
				}
			}
		}
	}

	&__right {
		display: flex;
		flex-direction: column;
		gap: get-rem(23);

		@media(min-width: 1360px) {
			gap: get-vw(23);
		}

		@media(min-width: 1921px) {
			gap: get-rem(23);
		}
	}

	&__links {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: get-rem(16);
		width: 100%;
		margin: get-rem(16) 0;

		@media(min-width: 560px) {
			width: calc(100% - #{get-rem(200)});
			margin: 0 0 get-rem(30);
			grid-gap: get-rem(16) get-rem(24);
		}

		@media(min-width: 661px) {
			grid-template-columns: repeat(4, 1fr);
		}

		@media(min-width: 992px) {
			grid-template-columns: repeat(2, 1fr);
			width: auto;
			margin: 0;
		}

		@media(min-width: 1360px) {
			grid-gap: get-vw(27) get-vw(39);
			grid-template-columns: repeat(3, 1fr);
		}

		@media(min-width: 1921px) {
			grid-gap: get-rem(27) get-rem(39);
		}

		a {
			display: block;
			font-size: get-rem(18);
			color: var(--color-white);

			@media(min-width: 1360px) {
				font-size: get-vw(24);
			}

			@media(min-width: 1921px) {
				font-size: get-rem(24);
			}

			&:hover {
				color: var(--color-gold);
			}
		}
	}

	&__links-sec {
		display: grid;
		grid-template-columns: 1fr;
		grid-gap: get-rem(16);

		@media(min-width: 1200px) {
			grid-template-columns: repeat(2, 1fr);
			grid-gap: get-rem(9) get-rem(40);
		}

		@media(min-width: 1360px) {
			grid-gap: get-vw(9) get-vw(52);
		}

		@media(min-width: 1921px) {
			grid-gap: get-rem(9) get-rem(52);
		}

		a {
			display: block;
			font-size: get-rem(18);
			color: rgba(#fff, 0.7);

			@media(min-width: 1360px) {
				font-size: get-vw(24);
			}

			@media(min-width: 1921px) {
				font-size: get-rem(24);
			}

			&:hover {
				color: var(--color-white);
			}
		}

		img {
			display: block;
			width: get-rem(97);
			height: get-rem(40);

			@media(min-width: 992px) {
				margin: get-rem(12) 0 0;
			}

			@media(min-width: 1360px) {
				width: get-vw(97);
				height: get-vw(40);
				margin: get-vw(12) 0 0;
			}

			@media(min-width: 1921px) {
				width: get-rem(97);
				height: get-rem(40);
				margin: get-rem(12) 0 0;
			}
		}
	}
}

.subscribe-form {
	position: relative;
	width: get-rem(280);
	height: get-rem(50);

	@media(min-width: 1360px) {
		width: get-vw(344);
		height: get-vw(62);
	}

	@media(min-width: 1921px) {
		width: get-rem(344);
		height: get-rem(62);
	}

	&__body {
		position: relative;
		display: flex;
		align-items: center;
		z-index: 2;
		height: calc(100% - 2px);
		left: 1px;
		top: 1px;
	}

	&__input {
		width: 100%;
		height: 100%;
		background-color: var(--color-black);
		color: var(--color-white);
		font-size: get-rem(16);
		padding: 0 0 0 get-rem(16);
		font-weight: 500;
		text-transform: uppercase;

		@media(min-width: 1360px) {
			font-size: get-vw(20);
			padding: 0 0 0 get-vw(20);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(20);
			padding: 0 0 0 get-rem(20);
		}

		&::placeholder {
			color: inherit;
			transition: opacity 0.2s linear;
		}

		&:focus {
			&::placeholder {
				opacity: 0.2;
			}
		}
	}

	&::after {
		display: none;
	}

	&__button {
		display: flex;
		justify-content: center;
		align-items: center;
		width: get-rem(80);
		font-size: get-rem(18);
		height: 100%;
		top: 0;
		right: 1px;
		flex-shrink: 0;
		color: var(--color-black);
		font-weight: 600;
		text-transform: uppercase;
		position: relative;

		@media(min-width: 1360px) {
			width: get-vw(104);
			font-size: get-vw(24);
		}

		@media(min-width: 1921px) {
			width: get-rem(104);
			font-size: get-rem(24);
		}

		span {
			position: relative;
			z-index: 2;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border: 1px solid transparent;
			background: linear-gradient(-180deg, var(--color-gold), var(--color-white)) border-box;
			transition: opacity 0.2s linear;
		}

		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border: 1px solid transparent;
			background: linear-gradient(0deg, var(--color-gold), var(--color-white)) border-box;
			transition: opacity 0.2s linear;
			opacity: 0;
		}

		.desktop-device & {
			&:hover {

				&::before {
					opacity: 0;
				}

				&::after {
					opacity: 1;
				}
			}
		}
	}

	&:hover {

	}
}
