@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.quest-form {
	display: flex;
	align-self: flex-start;
	flex-direction: column-reverse;
	gap: get-rem(24);

	@media(min-width: 768px) {
		flex-direction: row;
	}

	@media(min-width: 1280px) {
		gap: get-rem(45);
	}

	&__img {
		width: 40%;
		flex-shrink: 0;
		border: 1px solid var(--color-gold);
		align-self: stretch;
		display: none;
		overflow: hidden;

		@media(min-width: 768px) {
			display: block;
		}

		@media(min-width: 1366px) {
			width: 100%;
			max-width: get-vw(770);
		}

		@media(min-width: 1921px) {
			max-width: get-rem(770);
		}

		picture, img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__body {
		width: 100%;
		align-self: center;

		.title {
			margin: 0 0 get-rem(15);

			@media(min-width: 992px) {
				margin: 0 0 get-vw(30);
			}

			@media(min-width: 1921px) {
				margin: 0 0 get-rem(30);
			}
		}
	}

	&__form {
		display: grid;
		grid-template-columns: 1fr;
		grid-gap: get-rem(20);
		width: 100%;
		margin: get-rem(22) 0 0;

		@media(min-width: 560px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media(min-width: 1280px) {
			grid-gap: get-rem(21) get-rem(33);
		}

		.form-item--full, .button {
			@media(min-width: 560px) {
				grid-column: 1 / span 2;
			}

		}
	}
}

.form-item {
	position: relative;
	width: 100%;

	&__input {
		width: 100%;
		border: get-rem(2) solid var(--color-white);
		height: get-rem(60);
		font-size: get-rem(22);
		padding-left: get-rem(22);
		color: var(--color-white);
		background-color: var(--color-black);
		transition: border-color 0.2s linear;

		@media(min-width: 1440px) {
			height: get-rem(82);
			font-size: get-rem(26);
			padding-left: get-rem(30);
		}

		&:-webkit-autofill{
			transition: background-color 5000s ease-in-out 0s;
			-webkit-text-fill-color: white !important;
		}

		&::placeholder {
			color: inherit;
			transition: opacity 0.2s linear;
		}

		&:focus {
			border-color: var(--color-gold);

			&::placeholder {
				opacity: 0.2;
			}
		}
	}

	&__textarea {
		width: 100%;
		border: get-rem(2) solid var(--color-white);
		height: get-rem(200);
		font-size: get-rem(22);
		padding: get-rem(22) 0 0 get-rem(22);
		color: var(--color-white);
		background-color: var(--color-black);
		transition: border-color 0.2s linear;

		@media(min-width: 1440px) {
			height: get-rem(248);
			font-size: get-rem(26);
			padding: get-rem(22) 0 0 get-rem(30);
		}

		&:-webkit-autofill{
			transition: background-color 5000s ease-in-out 0s;
			-webkit-text-fill-color: white !important;
		}

		&::placeholder {
			color: inherit;
			transition: opacity 0.2s linear;
		}

		&:focus {
			border-color: var(--color-gold);
			outline: none;

			&::placeholder {
				opacity: 0.2;
			}
		}
	}
}
