  --color-white: #fff;
  --color-gold: #ffc37a;
  --color-dark: #0e0e0e;
  --color-gray: #d9d9d9;
}


body {
  margin: 0 auto;
  font-family: $mainFont;
  font-size: 16px;
  background-color: var(--color-black);
  overflow-x: hidden;
  font-weight: 400;
  position: relative;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;


  &::after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 900;
    background-color: var(--color-black);
    transition: opacity 0.7s ease;
  }

  &.loading {

    &::after {
      opacity: 0;
    }
  }

  &.loaded {
    &::after {
      display: none;
    }
  }
}
/**
 * Render the `main` element consistently in IE.
 */

main {
  display: block;
  flex: 1;
}

input[type="search"] {
  &::-webkit-search-cancel-button {
    display: none;
  }
}

picture {
  display: block;
}

/**
