@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.filter-list {
	margin: get-rem(32) 0 0;

	&__top {
		display: flex;
		margin: 0 0 get-rem(26);
		position: relative;
		z-index: 5;

		.select-item {

			@media(max-width: 560px) {
				width: 100%;
			}
		}
	}

	&__bottom {
		display: flex;
		justify-content: center;
		margin: get-rem(20) 0 0;

		@media(min-width: 560px) {
			margin: get-rem(47) 0 0;
		}

		.button {
			width: 100%;

			@media(min-width: 560px) {
				max-width: get-rem(245);
			}
		}
	}
}

.select-item {
	min-width: get-rem(212);

	&--sec {
		min-width: auto;
		width: 100%;

		@media(min-width: 992px) {
			max-width: get-rem(244);
		}
	}

	.choices {
		position: relative;
		z-index: 0;

		&.is-open {
			z-index: 10;
		}

		&__list--dropdown {
			background-color: var(--color-black);
			border-color: var(--color-white);
			border-top: 0;
			margin: get-rem(-1) 0 0;
			border-radius: 0;

			.choices__item {
				color: var(--color-white);
				font-size: get-rem(18);
			}

			.choices__item--selectable.is-highlighted, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
				background-color: rgba(#ffc37a, 0.7);
			}
		}

		&[data-type*=select-one] .choices__inner {
			padding: get-rem(12) get-rem(58) get-rem(12) get-rem(23);

			@media(min-width: 768px) {
				padding: get-rem(17) get-rem(58) get-rem(17) get-rem(23);
			}
		}

		&__inner {
			background-color: transparent;
			border: none;
			border-image-slice: 1;
			background-clip: padding-box;
			padding: 0;
			transition: background-color 0.2s linear;

			&:hover {
				background-color: rgba(#ffc37a, 0.1);
			}

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				border: 1px solid transparent;
				background: linear-gradient(180deg, var(--color-gold), var(--color-white)) border-box;
				-webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
				-webkit-mask-composite: destination-out;
				mask-composite: exclude;
				transition: opacity 0.2s linear;
			}

			.choices__item {
				font-weight: 600;
				color: var(--color-white);
				text-transform: uppercase;
				font-size: get-rem(20);

				@media(min-width: 768px) {
					font-size: get-rem(24);
				}

			}
		}

		.choices__list--single {
			padding: 0;
		}

		&[data-type*=select-one]::after {
			margin-top: 0;
			right: get-rem(23);
			transform: translateY(-50%);
			width: get-rem(15);
			height: get-rem(8);
			border: none;
			background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABEAAAAKCAYAAABSfLWiAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAABNSURBVHgBlc/BCQAwCANAR3GUjtZNO0pKQaEPJUYQfMRDDcCJdhPq5XPXYoACfQAScQVq81OI5lhgfHEXVF+uFpYENBBkgL0mV0CbAReflRk2qN9sJwAAAABJRU5ErkJggg==);
			background-size: 100% 100%;
			transition: transform 0.2s linear;
		}

		&.is-open[data-type*=select-one]::after {
			transform: translateY(-50%) scaleY(-1);
		}
	}
}


.options-block {

	&__top {
		display: grid;
		grid-template-columns: 1fr;
		grid-gap: get-rem(24);
		margin: get-rem(22) 0 0;

		@media(min-width: 480px) {
			grid-template-columns: repeat(2, 1fr);
			margin: get-rem(33) 0 0;
		}

		@media(min-width: 992px) {
			grid-template-columns: repeat(4, 1fr);
		}

		@media(min-width: 1366px) {
			grid-gap: get-rem(38);
		}

		.select-form {

			&:last-child {
				justify-content: flex-end;
			}
		}
	}
}

.select-form {
	display: flex;
	align-items: flex-start;
	flex-direction: column;
	gap: get-rem(10);

	@media(min-width: 992px) {
		gap: get-rem(20);
	}

	@media(min-width: 1366px) {
	    flex-direction: row;
		align-items: center;
	}

	&__text {
		font-size: get-rem(24);
		flex-shrink: 0;

		@media(min-width: 1200px) {
			font-size: get-vw(36);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(36);
		}
	}
}
