@use "../utils/_functions" as *;

.link {
	transition: width 0.2s linear, height 0.2s linear, color 0.2s linear;

	&--big {
		font-size: get-rem(14);
		line-height: 1.1;

		@media(min-width: 992px) {
			font-size: get-vw(26);

			.scroll & {
				font-size: get-vw(22);
			}
		}

		@media(min-width: 1921px) {
			font-size: get-rem(26);

			.scroll & {
				font-size: get-rem(22);
			}
		}
	}

	&--bigger {
		font-size: get-rem(18);
		line-height: 1.1;

		@media(min-width: 340px) {
			font-size: get-rem(20);
		}

		@media(min-width: 992px) {
			font-size: get-vw(32);

			.scroll & {
				font-size: get-vw(28);
			}
		}

		@media(min-width: 1921px) {
			font-size: get-rem(32);

			.scroll & {
				font-size: get-rem(28);
			}
		}
	}

	&--small {
		font-size: get-rem(16);
		text-transform: uppercase;
	}

	&--white {
		color: var(--color-white);

		&:hover {
			color: var(--color-gold);
		}

		&.is-active {
			color: var(--color-gold);
		}
	}

	&--ttu {
		text-transform: uppercase;
	}
}

.link-icon {
	display: flex;
	align-items: center;
	color: var(--color-white);
	gap: get-rem(10);

	@media(min-width: 1360px) {
		gap: get-vw(10);
	}

	@media(min-width: 1921px) {
		gap: get-rem(10);
	}

	&__icon {
		width: get-rem(20);
		height: get-rem(20);

		@media(min-width: 640px) {
			width: get-rem(24);
			height: get-rem(24);
		}

		@media(min-width: 1360px) {
			width: get-vw(29);
			height: get-vw(29);
		}

		@media(min-width: 1921px) {
			width: get-rem(29);
			height: get-rem(29);
		}
	}

	&__numb {
		font-size: get-rem(20);
		font-weight: bold;

		@media(min-width: 640px) {
			font-size: get-rem(24);
		}

		@media(min-width: 1360px) {
			font-size: get-vw(32);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(32);
		}
	}

	&__text {
		font-size: get-rem(20);

		@media(min-width: 640px) {
			font-size: get-rem(24);
		}

		@media(min-width: 1360px) {
			font-size: get-vw(32);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(32);
		}
	}

	&:hover {
		color: var(--color-gold);
	}
}
