@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.videos-block {
	display: grid;
	grid-template-columns: 1fr;
	grid-gap: get-rem(20) get-rem(36);

	@media(min-width: 560px) {
		grid-template-columns: repeat(2, 1fr);
	}

	@media(min-width: 768px) {
		grid-template-columns: repeat(3, 1fr);
	}
}

.video-item {
	text-align: center;

	&__img {
		width: 100%;
		height: 0;
		padding-bottom: 71.23%;
		position: relative;
		margin: 0 0 get-rem(26);

		@media(min-width: 1024px) {
			margin: 0 0 get-vw(26);
		}

		@media(min-width: 1921px) {
			margin: 0 0 get-rem(26);
		}

		&::after {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			z-index: 1;
			background-color: rgba(#000, 0.5);
		}

		picture {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			width: 100%;
			height: 100%;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__title {
		font-size: get-rem(22);
		font-weight: 600;
		color: var(--color-white);
		text-transform: uppercase;
		margin: 0 0 get-rem(6);

		@media(min-width: 1024px) {
			font-size: get-vw(35);
			margin: 0 0 get-vw(6);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(35);
			margin: 0 0 get-rem(6);
		}
	}

	&__sub {
		font-size: get-rem(20);
		font-weight: 600;
		color: rgba(#fff, 0.5);
		text-transform: uppercase;

		@media(min-width: 1024px) {
			font-size: get-vw(25);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(25);
		}
	}

	.play-button {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 3;
		transform: translate(-50%, -50%);
		width: get-rem(80);
		height: get-rem(80);
		border-radius: 100%;
		background-color: rgba(#fff, 0.16);
		transition: background-color 0.2s linear;

		@media(min-width: 1200px) {
			width: get-vw(152);
			height: get-vw(152);
		}

		@media(min-width: 1921px) {
			width: get-rem(152);
			height: get-rem(152);
		}

		img {
			width: get-rem(28);
			height: get-rem(28);

			@media(min-width: 1200px) {
				width: get-vw(57);
				height: get-vw(57);
			}

			@media(min-width: 1921px) {
				width: get-rem(57);
				height: get-rem(57);
			}
		}

		&__circle {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: 1;
			transform: translate(-50%, -50%);
			width: 80%;
			height: 80%;
			border-image-slice: 1;
			background-clip: padding-box;
			background-color: transparent;
			transition-property: width, height;
			transition: all 0.2s linear;

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				border: 2px solid transparent;
				border-radius: 100%;
				background: linear-gradient(180deg, var(--color-gold), var(--color-white)) border-box;
				-webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
				-webkit-mask-composite: destination-out;
				mask-composite: exclude;
				transition: opacity 0.2s linear;
			}
		}

		&:hover {
			background-color: rgba(#000, 0.16);

			.play-button__circle {
				width: 100%;
				height: 100%;
			}
		}

	}
}

.play-button {
	width: get-rem(152);
	height: get-rem(152);

	img {
		width: get-rem(57);
		height: get-rem(57);
	}
}

