@use "../utils/_functions" as *;
@use "../utils/_vars" as *;
//   
.button {
	display: flex;
	align-items: center;
	justify-content: center;
	transition: background-color 0.2s linear, color 0.2s linear;
	position: relative;

	&__text {
		position: relative;
		z-index: 2;
	}

	&--default {
		font-size: get-rem(20);
		line-height: 1;
		color: var(--color-white);
		text-transform: uppercase;
		padding: get-rem(15) get-rem(8);
		min-width: get-rem(208);
		font-weight: 500;

		@media(min-width: 1024px) {
			font-size: get-rem(24);
			padding: get-rem(19) get-rem(8);
		}
	}

	&--default-big {
		font-size: get-rem(24);
		line-height: 1;
		color: var(--color-white);
		text-transform: uppercase;
		padding: get-rem(18) get-rem(8);
		min-width: get-rem(208);
		font-weight: 500;

		@media(min-width: 992px) {
			font-size: get-rem(28);
			padding: get-rem(24) get-rem(8);
		}

		@media(min-width: 1440px) {
			font-size: get-rem(32);
			padding: get-rem(24) get-rem(8);
		}
	}
}


.gradient-border {
	border-image-slice: 1;
	background-clip: padding-box;
	background-color: transparent;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border: 1px solid transparent;
		background: linear-gradient(180deg, var(--color-gold), var(--color-white)) border-box;
		-webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
		-webkit-mask-composite: destination-out;
		mask-composite: exclude;
		transition: opacity 0.2s linear;
	}

	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border: 1px solid transparent;
		background: linear-gradient(180deg, var(--color-gold), var(--color-white)) border-box;
		transition: opacity 0.2s linear;
		opacity: 0;
	}

	&.button {
		&:hover {
			background-color: rgba(#ffc37a, 0.2);
		}
