@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.profile-item {
	display: flex;
	align-items: flex-start;
	flex-direction: column-reverse;
	gap: get-rem(20);

	+ .profile-item {
		margin: get-rem(50) 0 0;

		@media(min-width: 1320px) {
			margin: get-rem(76) 0 0;
		}
	}

	+ .double-grid {
		margin: get-rem(30) 0 0;

		@media(min-width: 992px) {
			margin: get-rem(50) 0 0;
		}

		@media(min-width: 1320px) {
			margin: get-rem(76) 0 0;
		}
	}

	@media(min-width: 992px) {
		flex-direction: row;
		gap: 3.2%;

		&--reverse {
			flex-direction: row-reverse;
		}

	}

	.simple-text--gray {
		margin: 0 0 get-rem(15);
	}

	.title {

		&--section-sec {
			margin: 0 0 get-rem(12);

			@media(min-width: 992px) and (max-width: 1920px)  {
				font-size: get-vw(64);
			}
		}

		&--middle {
			font-size: get-rem(30);
			margin: 0 0 get-rem(4);

			@media(min-width: 992px) and (max-width: 1920px)  {
				font-size: get-vw(48);
			}
		}
	}

	&__img {
		width: 100%;
		flex-shrink: 0;
		border: 1px solid var(--color-gold);
		position: sticky;
		top: get-rem(100);
		overflow: hidden;

		@media(min-width: 992px) {
			max-width: get-rem(480);
		}

		@media(min-width: 1200px) {
			max-width: get-rem(550);
		}

		@media(min-width: 1366px) {
			max-width: get-vw(774);
		}

		@media(min-width: 1921px) {
			max-width: get-rem(774);
		}

		picture, img {
			width: 100%;
			height: auto;
			transform: scale(1);
		}
	}

	&__body {
		align-self: center;
	}

	&__table {
		display: flex;
		flex-direction: column;
		gap: get-rem(12);
		margin: get-rem(8) 0 0;

		&--sec {
			@media(min-width: 1200px) {
			    flex-direction: row;
			}
		}

		+ .simple-text {
			margin: get-rem(15) 0 0;
		}
	}

	&__table-item {
		display: flex;
		align-items: center;
		font-size: get-rem(20);

		@media(min-width: 1200px) {
			font-size: get-rem(24);
		}

		@media(min-width: 1200px) {
			font-size: get-rem(28);
		}

		span {

			&:nth-child(1) {
				color: rgba(#fff, 0.7);
			}

			&:nth-child(2) {
				color: var(--color-white);
				font-weight: bold;
				margin: 0 0 0 get-rem(6);
			}
		}
	}

	&--sec {
		align-items: flex-start;

		.profile-item {

			&__img {
				position: sticky;
				top: 0;
			}

			&__body {
				padding: get-rem(10) 0 0;
			}
		}
	}

	&--no-border {

		.profile-item {

			&__img {
				border-color: transparent;
			}
		}
	}

	&--shorter {
		.profile-item__img {
			@media(min-width: 1366px) {
				max-width: get-vw(728);
			}

			@media(min-width: 1921px) {
				max-width: get-rem(728);
			}
		}
	}

	&--mini {

		.profile-item__table-item {

			@media(max-width: 1920px) {
			    font-size: get-vw(28);
			}

			@media(max-width: 1280px) {
				font-size: get-rem(21);
			}
		}

		.profile-item__img {
			max-width: get-rem(420);

			@media(min-width: 1366px) {
				max-width: get-vw(547);
			}

			@media(min-width: 1921px) {
				max-width: get-rem(547);
			}
		}
	}
}
