@use "../utils/_functions" as *;

.rooms-block {
	margin: get-rem(30) 0 0;

	@media(min-width: 768px) {
		margin: get-rem(50) 0 0;
	}

	&__bottom {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: get-rem(20) 0 0;

		@media(min-width: 560px) {
			margin: get-rem(43) 0 0;
		}

		.button {
			@media(max-width: 560px) {
				width: 100%;
			}
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: get-rem(10);

		@media(min-width: 640px) {
			grid-template-columns: repeat(3, 1fr);
		}

		@media(min-width: 992px) {
			grid-template-columns: repeat(3, 1fr);
			grid-gap: get-vw(40);
		}

		@media(min-width: 1921px) {
			grid-gap: get-rem(40);
		}
	}
}

.title-sec {
	max-width: get-rem(925);
	margin: 0 auto get-rem(30);
	text-align: center;

	@media(min-width: 768px) {
		margin: 0 auto get-rem(53);
	}

	.title {
		text-transform: capitalize;
	}
}
