@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.contacts-grid {

	&__list {
		display: grid;
		justify-content: space-between;
		grid-gap: get-rem(30) get-rem(12);
		grid-template-columns: repeat(2, 1fr);
		margin: get-rem(25) 0 0;

		@media(min-width: 480px) {
			grid-gap: get-rem(30);
		}

		@media(min-width: 744px) {
			grid-template-columns: repeat(3, 1fr);
		}

		@media(min-width: 1100px) {
			grid-template-columns: repeat(4, 1fr);
		}

		@media(min-width: 1560px) {
			grid-template-columns: repeat(4, #{get-rem(350)});
		}
	}

	&__item {

		&:last-child {
			@media(min-width: 744px) and (max-width: 1099px) {
			    grid-column: 1/ span 3;
			}
		}

		.title {
			margin: 0 0 get-rem(8);

			@media(max-width: 480px) {
				font-size: 6.5vw;
			}
		}

		ul {
			padding: 0;
			margin: 0;

			li {
				font-family: $secFont;
				font-size: 4.2vw;
				line-height: 1.4;
				color: var(--color-white);
				text-transform: capitalize;

				@media(min-width: 460px) {
					font-size: get-rem(18);
				}

				@media(min-width: 1280px) {
					font-size: get-rem(20);
				}
			}
		}

		p {
			font-family: $secFont;
			font-size: 4.2vw;
			line-height: 1.4;
			text-transform: capitalize;
			color: rgba(#fff, 0.5);
			margin: 0;

			@media(min-width: 460px) {
				font-size: get-rem(18);
			}

			@media(min-width: 1280px) {
				font-size: get-rem(20);
			}
		}
	}
}

.map-block {
	width: 100%;
	height: 0;
	padding-bottom: 32.7%;
	min-height: get-rem(440);

	.gmnoprint.gm-style-mtc-bbw {
		display: none;
	}

	.gm-fullscreen-control {
		display: none;
	}

	.gm-bundled-control-on-bottom {
		display: none;
	}

	.gm-style-cc,.gmnoprint  {
		display: none;
	}
}
