@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 30;
	background-color: var(--color-black);
	padding: get-rem(6) 0;
	border-bottom: 1px solid transparent;

	@media(min-width: 992px) {
		padding: get-rem(10) 0;
	}

	&__body {
		display: grid;
		align-items: center;
		grid-template-columns: get-rem(130) 1fr get-rem(60);

		@media(min-width: 768px) {
			grid-template-columns: get-rem(130) 1fr get-rem(130);
		}

		@media(min-width: 992px) {
			grid-template-columns: get-vw(240) 1fr get-vw(240);
		}

		@media(min-width: 1921px) {
			grid-template-columns: get-vw(240) 1fr get-vw(240);
		}
	}

	&__menu {
		display: none;
		align-items: center;
		justify-content: center;
		gap: get-rem(20);

		@media(min-width: 768px) {
			display: flex;
		}

		@media(min-width: 992px) {
			gap: get-vw(73);
		}

		@media(min-width: 1921px) {
			gap: get-rem(73);
		}

		+ .link {
			text-align: right;

			@media(min-width: 768px) {
				text-align: right;
			}
		}
	}
}

.burger {
	display: flex;
	justify-content: center;
	align-items: center;
	width: get-rem(36);
	height: get-rem(36);
	margin-left: auto;

	@media(min-width: 768px) {
		display: none;
	}

	svg {
		width: 100%;
		height: 100%;
	}
}

.logo {
	display: flex;
	justify-content: center;
	align-items: center;

	&__hidden {
		display: none;
		max-width: get-rem(180);

		.scroll & {
			@media(min-width: 768px) {
				display: block;
			}
		}
	}

	&--header {
		width: get-rem(120);
		height: get-rem(60);
		transition: all 0.2s linear;
		position: relative;

		@media(min-width: 1360px) {
			width: get-vw(218);
			height: get-vw(119);
		}

		@media(min-width: 1921px) {
			width: get-rem(218);
			height: get-rem(119);
		}

		.scroll & {
			@media(min-width: 768px) {
				height: get-rem(60);

				picture {
					position: absolute;
					opacity: 0;
					transition: opacity 0.2s linear;
				}
			}
		}
	}

	&--footer {
		width: get-rem(160);
		height: get-rem(80);
		flex-shrink: 0;

		@media(min-width: 992px) {
		    flex-shrink: initial;
		}

		@media(min-width: 992px) {
			width: get-vw(218);
			height: get-vw(119);
		}

		@media(min-width: 1921px) {
			width: get-rem(218);
			height: get-rem(119);
		}
	}

	img {
		width: 100%;
		height: 100%;
	}
}

.mobile-menu {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 50;
	width: 100%;
	height: 100%;
	background-color: var(--color-black);
	opacity: 0;
	pointer-events: none;
	visibility: hidden;
	transition: opacity 0.2s linear, visibility 0.2s linear;

	&.show {
		opacity: 1;
		pointer-events: all;
		visibility: visible;
	}

	&__top {
		position: sticky;
		top: 0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: get-rem(20);
		padding: get-rem(6) get-rem(20) get-rem(6) get-rem(12);
		border-bottom: 1px solid rgba(#fff, 0.4);

		.close-btn {
			color: rgba(#fff, 0.4);
		}
	}

	&__menu {
		display: flex;
		flex-direction: column;
		gap: get-rem(24);
		padding: get-rem(24) get-rem(12) get-rem(16);
		height: calc(100% - #{get-rem(73)});
		overflow: auto;

		.link--big {
			font-size: get-rem(26);
			padding: get-rem(2) 0;
		}
	}
}
