@use "../utils/_functions" as *;

.signature-block {
	padding: get-rem(40) 0;
	background-size: cover;
	background-position: right center;
	background-repeat: no-repeat;
	position: relative;

	@media(min-width: 768px) {
		padding: get-vw(91) 0;
	}

	@media(min-width: 1921px) {
		padding: get-rem(91) 0;
	}

	&::after {
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: 1;
		background-color: rgba(#000, 0.4);

		@media(min-width: 768px) {
			background-color: rgba(#000, 0.2);
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		position: relative;
		z-index: 2;
	}

	&__logo {
		width: get-rem(284);
		height: get-rem(90);
		margin: 0 0 get-rem(25);

		@media(min-width: 992px) {
			width: get-vw(592);
			height: get-vw(188);
		}

		@media(min-width: 1921px) {
			width: get-rem(592);
			height: get-rem(188);
		}

		picture, img {
			width: 100%;
			height: 100%;
		}
	}

	.simple-text {
		max-width: get-rem(820);
		margin: 0 0 get-rem(30);

		@media(min-width: 992px) {
			margin: 0 0 get-rem(54);
		}
	}

	.button {
		width: 100%;

		@media(min-width: 560px) {
			max-width: get-rem(243);
		}
	}
}
