@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.qr-block {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	flex-direction: column;
	gap: get-rem(40);

	@media(min-width: 992px) {
	    flex-direction: row;
	}

	&__img {
		width: 100%;
		flex-shrink: 0;
		border: 1px solid var(--color-gold);

		@media(min-width: 992px) {
			max-width: get-rem(460);
		}

		@media(min-width: 1200px) {
			max-width: get-vw(728);
		}

		@media(min-width: 1921px) {
			max-width: get-rem(728);
		}

		picture, img {
			width: 100%;
			height: auto;
		}
	}

	&__left {
		width: 100%;
		max-width: get-rem(953);
	}

	&__code {
		width: get-rem(160);
		height: get-rem(160);
		margin: get-rem(15) 0;

		@media(min-width: 768px) {
			width: get-rem(200);
			height: get-rem(200);
		}

		@media(min-width: 1200px) {
			width: get-vw(284);
			height: get-vw(284);
			margin: get-vw(30) 0 get-vw(28);
		}

		@media(min-width: 1921px) {
			width: get-rem(284);
			height: get-rem(284);
			margin: get-rem(30) 0 get-rem(28);
		}

		picture, img {
			width: 100%;
			height: 100%;
		}
	}

	&__board {
		font-family: $secFont;
		border: 1px solid var(--color-white);
		font-size: get-rem(24);
		color: var(--color-white);
		display: flex;
		padding: get-rem(16);
		margin: 0 0 get-rem(28);
		line-height: 1.35;
		text-transform: capitalize;

		@media(min-width: 992px) {
			font-size: get-rem(32);
			padding: get-rem(16) get-rem(24);
			margin: 0 0 get-rem(28);
		}

		@media(min-width: 1200px) {
			font-size: get-vw(48);
			padding: get-vw(26) get-vw(34);
			margin: 0 0 get-vw(28);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(48);
			padding: get-rem(26) get-rem(34);
			margin: 0 0 get-rem(28);
		}
	}
}
