@use "../utils/_functions" as *;

.options-block {
	position: relative;
	z-index: 3;
}

.schedule-block {
	margin: get-rem(25) 0 0;

	&__top {
		display: grid;
		grid-template-columns: 1fr;
		position: sticky;
		top: get-rem(72);
		background-color: var(--color-black);
		z-index: 2;

		@media(min-width: 560px) {
			grid-template-columns: minmax(#{get-rem(100)}, 1.02fr) 5.6fr;
		}

		@media(min-width: 992px) {
			top: get-rem(80);
		}

		.title--middle {

			@media(min-width: 992px) {
				font-size: get-vw(48);
			}

			@media(min-width: 1921px) {
				font-size: get-rem(48);
			}
		}
	}

	&__pic {
		width: 100%;
		grid-column: 1/ span 3;
		margin: get-rem(16) 0 0;

		@media(min-width: 560px) {
			margin: get-rem(27) 0 0;
		}

		picture, img {
			width: 100%;
			height: auto;
		}
	}

	&__days {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		@media(min-width: 992px) {
			display: grid;
			grid-template-columns: 0.68fr 1fr get-vw(253);
		}

		@media(min-width: 1921px) {
			grid-template-columns: 0.68fr 1fr get-rem(253);
		}
	}

	&__item {
		display: grid;
		grid-template-columns: minmax(#{get-rem(100)}, 1.02fr) minmax(#{get-rem(170)}, 1.03fr) minmax(#{get-rem(170)}, 1.35fr) minmax(#{get-rem(170)}, 1.1fr) minmax(#{get-rem(170)}, 1.35fr) minmax(180px, 1fr);
	}

	&__body {
		position: relative;
		border-image-slice: 1;
		background-clip: padding-box;
		background-color: transparent;
		max-height: get-rem(560);
		overflow: auto;

		@media(min-width: 992px) {
			max-height: get-vw(819);
		}

		@media(min-width: 1921px) {
			max-height: get-rem(819);
		}

		&::-webkit-scrollbar-track
		{
			border-radius: get-rem(20);
			background-color: var(--color-gray);
			width: get-rem(5);
		}

		&::-webkit-scrollbar
		{
			width: get-rem(5);
			height: get-rem(5);
			background-color: var(--color-gray);
		}

		&::-webkit-scrollbar-thumb
		{
			border-radius: get-rem(20);
			width: get-rem(5);
			height: get-rem(5);
			background-color: var(--color-gold);
		}


	}

	&__body-wrap {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border: 1px solid transparent;
			background: linear-gradient(180deg, var(--color-gold), rgba(#d9d9d9, 0.5)) border-box;
			-webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
			-webkit-mask-composite: destination-out;
			mask-composite: exclude;
			transition: opacity 0.2s linear;
		}
	}

	&__cell {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		position: relative;
		border: 1px solid rgba(#d9d9d9, 0.5);
		min-height: get-rem(90);
		gap: get-rem(9);
		padding: get-rem(14) get-rem(12);

		@media(min-width: 992px) {
			min-height: get-vw(162);
			gap: get-vw(9);
			padding: get-vw(14) get-vw(12);
		}

		@media(min-width: 1921px) {
			min-height: get-rem(162);
			gap: get-rem(9);
			padding: get-rem(14) get-rem(12);
		}

		.mini-board {
			transform: translateY(3px);
		}

		.simple-text {
			text-align: center;
			word-break: break-all;
			@media(max-width: 991px) {
			    font-size: get-rem(18);
			}
		}
	}

	.schedule-day:last-child {
	}
}

.schedule-day {
	display: flex;
	flex-direction: column;
	align-items: flex-start;

	.title {
		text-transform: capitalize;

		@media(max-width: 991px) {
		    font-size: get-rem(28);
		}

		@media(max-width: 480px) {
		    font-size: 7vw;
		}
	}

	&__day {
		color: rgba(#fff, 0.5);
		font-size: get-rem(16);
		margin-bottom: get-rem(4);

		@media(min-width: 992px) {
			font-size: get-vw(20);
			margin-bottom: 0;
		}

		@media(min-width: 1921px) {
			font-size: get-rem(20);
		}
	}

	&__time {
		color: var(--color-white);
		font-size: get-rem(18);

		@media(min-width: 560px) {
			font-size: get-rem(22);
		}

		@media(min-width: 992px) {
			font-size: get-vw(30);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(30);
		}
	}
}

.mini-board {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: get-rem(2) get-rem(14);
	font-size: get-rem(14);
	border: get-rem(5) solid var(--color-black);
	color: var(--color-white);
	text-transform: capitalize;
	background-image: linear-gradient(45deg, #ffc37b, #452806);

	@media(min-width: 992px) {
		padding: get-vw(2) get-vw(14);
		font-size: get-vw(18);
		border: get-vw(5) solid var(--color-black);
	}

	@media(min-width: 1921px) {
		padding: get-rem(2) get-rem(14);
		font-size: get-rem(18);
		border: get-rem(5) solid var(--color-black);
	}
}

.gold-board {
	width: 100%;
	max-width: get-rem(160);
	font-size: get-rem(20);
	border: get-rem(5) solid var(--color-black);
	display: flex;
	justify-content: space-between;
	color: var(--color-white);
	background-image: linear-gradient(45deg, #ffc37b, #452806);

	@media(min-width: 992px) {
		max-width: get-vw(244);
		font-size: get-vw(24);
		border: get-vw(5) solid var(--color-black);
	}

	@media(min-width: 1921px) {
		max-width: get-rem(244);
		font-size: get-rem(24);
		border: get-rem(5) solid var(--color-black);
	}

	&__time {
		color: var(--color-white);
		padding: get-rem(12) get-rem(16);
		transition: text-shadow 0.2s linear;

		@media(min-width: 992px) {
			padding: get-vw(12) get-vw(29);
		}

		@media(min-width: 1921px) {
			padding: get-rem(12) get-rem(29);
		}

		&:hover {
			text-shadow: 0 0 4px var(--color-white),0 0 4px var(--color-white);
		}
	}
}
