@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.services-list {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: get-rem(60) get-rem(10);

	@media(min-width: 560px) {
		grid-gap: get-rem(60) get-rem(20);
	}

	@media(min-width: 768px) {
		grid-template-columns: repeat(3, 1fr);
	}

	@media(min-width: 1200px) {
		grid-gap: get-vw(142) get-vw(34);
		grid-template-columns: repeat(4, 1fr);
	}

	@media(min-width: 1921px) {
		grid-gap: get-rem(142) get-rem(34);
	}

	.service-item {

		&:last-child {
			grid-column: 1/ span 2;

			@media(min-width: 768px) {
				grid-column: 1/ span 3;
			}

			@media(min-width: 1200px) {
				grid-column: 3/ span 2;
			}
		}
	}
}

.service-item {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border: 1px solid var(--color-gold);
	padding: get-rem(40) get-rem(10) get-rem(20);
	text-align: center;
	position: relative;

	@media(min-width: 560px) {
		padding: get-rem(49) get-rem(16) get-rem(30);
	}

	@media(min-width: 1200px) {
		padding: get-vw(49) get-vw(16) get-vw(30);
	}

	@media(min-width: 1921px) {
		padding: get-rem(49) get-rem(16) get-rem(30);
	}

	&__board {
		display: flex;
		justify-content: center;
		align-items: center;
		text-transform: uppercase;
		color: var(--color-white);
		font-size: get-rem(20);
		border: get-rem(5) solid var(--color-black);
		height: get-rem(48);
		width: 80%;
		max-width: get-rem(200);
		position: absolute;
		top: get-rem(-24);
		left: 50%;
		z-index: 1;
		transform: translateX(-50%);
		background-image: linear-gradient(45deg, #ffc37b, #452806);

		@media(min-width: 560px) {
			height: get-rem(55);
			top: get-rem(-27);
		}

		@media(min-width: 1200px) {
			font-size: get-vw(32);
			height: get-vw(62);
			width: get-vw(244);
			top: get-vw(-27);
			max-width: initial;
		}

		@media(min-width: 1921px) {
			font-size: get-rem(32);
			height: get-rem(62);
			width: get-rem(244);
			top: get-rem(-27);
		}
	}

	&__price {
		font-weight: 500;
		font-size: get-rem(42);
		margin: 0 0 get-rem(5);
		line-height: 1;

		@media(min-width: 560px) {
			font-size: get-rem(50);
		}

		@media(min-width: 1200px) {
			font-size: get-vw(88);
			margin: 0 0 get-vw(5);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(88);
			margin: 0 0 get-rem(5);
		}
	}

	&__sub {
		font-size: get-rem(20);

		@media(min-width: 1200px) {
			font-size: get-vw(32);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(32);
		}
	}

	&__ul {
		margin: get-rem(13) 0 get-rem(17);
		gap: get-rem(8);
		padding: 0;
		display: flex;
		flex-direction: column;

		@media(min-width: 1200px) {
			margin: get-vw(13) 0 get-rem(17);
			gap: get-vw(8);
		}

		@media(min-width: 1921px) {
			margin: get-rem(13) 0 get-rem(17);
			gap: get-rem(8);
		}

		li {
			color: var(--color-white);
			font-size: get-rem(16);
			text-transform: capitalize;

			@media(min-width: 1200px) {
				font-size: get-vw(20);
			}

			@media(min-width: 1921px) {
				font-size: get-rem(20);
			}
		}
	}

	.button {
		width: 100%;
		max-width: get-rem(243);
		min-width: auto;
		margin: auto 0 0;
	}
}
