.container {
	width: 100%;
	margin: 0 auto;
	max-width: get-rem(1794);
	padding: 0 get-rem(12);
}

.preview-main {
	margin: 30px 0;

	&__top {
		display: flex;
		justify-content: center;
		font-size: get-rem(48);
		text-transform: uppercase;
		text-align: center;
		margin: get-rem(80) 0 0;
		color: var(--color-white);

		@media(max-width: 1024px) {
			font-size: get-rem(32);
			margin: get-rem(0) 0 0;
			flex-direction: column;
		}

		strong {
			display: block;
			color: var(--color-white);
			margin: 0 10px 0 0;
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		max-width: 900px;
		margin: get-rem(50) auto 0;
		width: 100%;
		grid-gap: 20px;
		padding: 0 20px;

		@media(max-width: 1024px) {
			margin: get-rem(30) auto 0;
			grid-template-columns: repeat(2, 1fr);
		}

		.preview-main__link {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 50px;
			color: #fff;
			text-decoration: none;
			text-transform: uppercase;
			background-color: var(--main-red-color);
			letter-spacing: 0.2em;
			font-size: 14px;
			padding: 0 8px;
			transition: background-color 0.2s linear, border-color 0.2s linear, transform 0.2s linear;
			border: 1px solid #fff;
			text-align: center;

			@media(max-width: 560px) {
				font-size: 12px;
			}

			&:hover {
				background-color: rgba(#fff, 0.2);
				transform: scale(0.97);
			}
		}
	}
}
