@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.rooms-card {
	margin: 0 0 get-rem(50);

	@media(min-width: 640px) {
		margin: 0 0 get-rem(75);
	}

	@media(min-width: 1200px) {
		margin: 0 0 get-rem(104);
	}

	&__img {
		width: 100%;
		height: 0;
		padding-bottom: 46.6%;
		border: 1px solid var(--color-gold);
		margin: 0 0 -30.1%;
		min-height: get-rem(340);
		position: relative;

		@media(min-width: 640px) {
			margin: 0 0 -15.1%;
		}

		picture {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			width: 100%;
			height: 100%;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__board {
		width: 95%;
		max-width: get-rem(1572);
		position: relative;
		z-index: 2;
		margin: 0 auto 0;
		padding: get-rem(20) get-rem(16);
		border-image-slice: 1;
		background-clip: padding-box;
		background-color: var(--color-dark);

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 40%;
			border: 1px solid transparent;
			border-bottom: none;
			background: linear-gradient(180deg, var(--color-gold), var(--color-black)) border-box;
			-webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
			-webkit-mask-composite: destination-out;
			mask-composite: exclude;
			transition: opacity 0.2s linear;
		}

		@media(min-width: 768px) {
			padding: get-rem(40) get-rem(30);
		}

		@media(min-width: 1200px) {
			padding: get-vw(72) get-vw(60);
		}

		@media(min-width: 1921px) {
			padding: get-rem(72) get-rem(60);
		}

		.title {
			margin: 0 0 get-rem(11);
		}

		.default-ul {

			@media(min-width: 768px) {
				padding: 0 0 0 2%;
			}
		}
	}
}


.double-grid {
	display: grid;
	grid-template-columns: 1fr;
	grid-gap: get-rem(30);

	@media(min-width: 992px) {
		grid-gap: get-vw(76);
		grid-template-columns: repeat(2, 1fr);
	}

	@media(min-width: 1921px) {
		grid-gap: get-rem(76);
	}
}

.room-mini {
	display: flex;
	align-items: center;
	flex-direction: column;
	gap: get-rem(16);

	@media(min-width: 560px) {
	    flex-direction: row;
	}

	@media(min-width: 1024px) {
		gap: get-vw(27);
	}

	@media(min-width: 1921px) {
		gap: get-rem(27);
	}

	&__img {
		width: 100%;
		height: auto;
		flex-shrink: 0;
		border: 1px solid transparent;
		transition: border-color 0.2s linear;

		@media(min-width: 560px) {
			width: get-rem(240);
			height: get-rem(140);
		}

		@media(min-width: 992px) {
			width: get-vw(328);
			height: get-vw(196);
		}

		@media(min-width: 1921px) {
			width: get-rem(328);
			height: get-rem(196);
		}

		picture, img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}

	&__title {
		font-weight: 700;
		font-size: get-rem(20);
		margin: 0 0 get-rem(10);
		text-transform: uppercase;
		color: var(--color-white);
		display: -webkit-box;
		-webkit-line-clamp: 1;
		-webkit-box-orient: vertical;
		overflow: hidden;
		transition: color 0.2s linear;

		@media(min-width: 1024px) {
			font-size: get-vw(32);
			margin: 0 0 get-vw(18);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(32);
			margin: 0 0 get-rem(18);
		}
	}

	&__text {
		font-size: get-rem(16);
		line-height: 1.4;
		color: rgba(#fff, 0.7);
		text-transform: capitalize;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;

		@media(min-width: 1024px) {
			font-size: get-vw(24);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(24);
		}
	}

	.link {
		margin: auto 0 0;
		transition: color 0.2s linear;
	}

	&[href]:hover {

		.news-mini {

			&__title {
				color: rgba(#ffc37a, 0.6);
			}

			&__img {
				border-color: var(--color-gold)
			}
		}
	}
}
