@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.news-top {
	display: grid;
	align-items: center;
	grid-template-columns: 1fr;
	grid-gap: get-rem(24);
	margin: 0 0 get-rem(36);

	@media(min-width: 640px) {
		grid-template-columns: 1.1fr 1fr;
	}

	@media(min-width: 768px) {
		grid-template-columns: 1.3fr 1fr;
		margin: 0 0 get-rem(36);
	}

	@media(min-width: 1024px) {
		gap: get-vw(48);
		grid-template-columns: 1fr 1.1fr 1fr;
		margin: 0 0 get-vw(60);
	}

	@media(min-width: 1921px) {
		gap: get-rem(48);
		margin: 0 0 get-rem(60);
	}

	.card-item {

		@media(min-width: 640px) {
			padding-bottom: 0;
			height: 100%;
		}

		.title {
			font-family: $mainFont;
		}

		.default-sub {
			text-transform: capitalize;
			color: rgba(#fff, 0.7);
			font: $secFont;
		}

		&__body {
			gap: get-rem(9);
			bottom: get-rem(16);
			left: get-rem(16);

			@media(min-width: 1024px) {
				bottom: get-vw(52);
				left: get-vw(36);
			}

			@media(min-width: 1921px) {
				bottom: get-rem(52);
				left: get-rem(36);
			}
		}
	}

	&__left {
		display: flex;
		flex-direction: column;
		gap: get-rem(24);
		padding: get-rem(20) 0;

		@media(min-width: 640px) {
			gap: get-rem(49);
		}

		@media(min-width: 1024px) {
			gap: get-vw(49);
			padding: get-rem(53) 0;
		}

		@media(min-width: 1921px) {
			gap: get-rem(49);
		}
	}

	&__right {
		display: flex;
		flex-direction: row;
		align-self: flex-start;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: get-rem(30) 0;

		@media(min-width: 992px) {
			flex-direction: column;
			justify-content: initial;
			flex-wrap: initial;
			gap: get-rem(30);
			padding: 0;
		}

		@media(min-width: 640px) and (max-width: 1023px) {
			grid-column: 1/ span 2;
		}

		@media(min-width: 1024px) {
			gap: get-vw(49);
		}

		@media(min-width: 1921px) {
			gap: get-rem(49);
		}

		@media(max-width: 991px) {
		    .news-mini {
			    width: 48.4%;
		    }
		}
	}
}

.news-list {
	display: flex;
	flex-direction: column;
	gap: 25px;

	&__grid {
		display: grid;
		align-items: center;
		grid-template-columns: 1fr;
		gap: get-rem(20) get-rem(30);

		@media(min-width: 560px) {
			grid-template-columns: 1fr 1fr;
		}

		@media(min-width: 992px) {
			grid-template-columns: repeat(3, 1fr);
		}

		@media(min-width: 1200px) {
			gap: get-vw(58) get-vw(82);

			&--secondary {
				gap: get-vw(20) get-vw(82);
			}
		}

		@media(min-width: 1921px) {
			gap: get-rem(58) get-rem(82);

			&--secondary {
				gap: get-rem(20) get-rem(82);

			}
		}
	}
}

.news-mini {
	display: flex;
	align-items: center;
	gap: get-rem(16);

	@media(min-width: 1024px) {
		gap: get-vw(27);
	}

	@media(min-width: 1921px) {
		gap: get-rem(27);
	}

	&__img {
		width: get-rem(100);
		height: get-rem(100);
		flex-shrink: 0;
		border: 1px solid transparent;
		transition: border-color 0.2s linear;

		@media(min-width: 1024px) {
			width: get-vw(160);
			height: get-vw(160);
		}

		@media(min-width: 1921px) {
			width: get-rem(160);
			height: get-rem(160);
		}

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}

	&__title {
		font-size: get-rem(20);
		margin: 0 0 get-rem(10);
		text-transform: uppercase;
		color: var(--color-white);
		display: -webkit-box;
		-webkit-line-clamp: 1;
		-webkit-box-orient: vertical;
		overflow: hidden;
		transition: color 0.2s linear;

		@media(min-width: 1024px) {
			font-size: get-vw(24);
			margin: 0 0 get-vw(18);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(24);
			margin: 0 0 get-rem(18);
		}
	}

	&__text {
		font-size: get-rem(16);
		margin: 0 0 get-rem(10);
		line-height: 1.3;
		color: rgba(#fff, 0.7);
		text-transform: capitalize;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		overflow: hidden;

		@media(min-width: 1024px) {
			font-size: get-vw(20);
			margin: 0 0 get-vw(15);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(20);
			margin: 0 0 get-rem(15);
		}
	}

	.link {
		margin: auto 0 0;
		transition: color 0.2s linear;
	}

	&:hover {

		.news-mini {

			&__title {
				color: rgba(#ffc37a, 0.6);
			}

			&__img {
				border-color: var(--color-gold)
			}
		}
	}
}
