@use "../utils/_functions" as *;

.top-block {
	position: relative;
	overflow: hidden;

	&__logo {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: calc(100% - #{get-rem(24)});
		z-index: 2;
		max-width: get-rem(500);

		@media(min-width: 1280px) {
			max-width: get-vw(824);
		}

		@media(min-width: 1921px) {
			max-width: get-rem(824);
		}

		picture, img {
			width: 100%;
			height: auto;
		}
	}

	> .title--large {
		width: 100%;
		max-width: get-rem(1300);
		margin: 0 auto;
		text-align: center;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 2;
		padding: 0 get-rem(16);
	}

	&__body {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		text-align: center;
		gap: get-rem(24);
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: calc(100% - #{get-rem(24)});
		z-index: 2;

		.title {
			line-height: 0.9;
		}

		.button {
			min-width: get-rem(244);
		}
	}

	&__img {
		width: 100%;
		height: 100%;
		position: relative;

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			width: 100%;
			height: 100%;
			background-color: rgba(#000, 0.75);
		}

		picture, img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&--lighter {
		.top-block__img::after {
			background-color: rgba(#000, 0.25);
		}
	}
}
