@use "../utils/_functions" as *;

.card-list {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: get-rem(10);

	@media(min-width: 640px) {
		grid-template-columns: repeat(3, 1fr);
	}

	@media(min-width: 992px) {
		grid-template-columns: repeat(4, 1fr);
		grid-gap: get-vw(22) get-vw(32);
	}

	@media(min-width: 1921px) {
		grid-gap: get-rem(22) get-rem(32);
	}
}

.tours-block {

	&__list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-template-rows: auto;
		grid-gap: get-rem(10);

		@media(min-width: 992px) {
			grid-template-columns: repeat(2, 0.98fr) repeat(2, 1.05fr);
			grid-gap: 0 2%;
		}

		.card-item {
			border: none;

			&:nth-child(n + 3):not(:last-child){

				@media(min-width: 992px) {
					margin-top: 8%;
				}
			}

			&:not(:last-child) {
				padding-bottom: 101.35%;
			}

			&:last-child {
				align-self: stretch;
				grid-column: 1 / span 2;
				padding-bottom: 70%;

				@media(min-width: 560px) {
					padding-bottom: 40%;
				}

				@media(min-width: 992px) {
					grid-column: 3 / span 2;
					grid-row: 1 / span 2;
					padding-bottom: 0;
					height: 100%;
				}

				.card-item__body {
					bottom: get-rem(12);
					left: get-rem(12);

					@media(min-width: 992px) {
						bottom: get-vw(47);
						left: get-vw(53);
					}

					@media(min-width: 1921px) {
						bottom: get-rem(47);
						left: get-rem(53);
					}
				}

				.default-title {
					font-size: get-rem(24);

					@media(min-width: 460px) {
						font-size: get-rem(30);
					}

					@media(min-width: 1024px) {
						font-size: get-vw(48);
					}

					@media(min-width: 1921px) {
						font-size: get-rem(48);
					}
				}

				.default-sub {
					font-size: get-rem(20);

					@media(min-width: 460px) {
						font-size: get-rem(24);
					}

					@media(min-width: 1024px) {
						font-size: get-vw(32);
					}

					@media(min-width: 1921px) {
						font-size: get-rem(32);
					}
				}
			}
		}
	}

	&__bottom {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: get-rem(20) 0 0;

		@media(min-width: 560px) {
			margin: get-rem(43) 0 0;
		}

		.button {
			@media(max-width: 560px) {
				width: 100%;
			}
		}
	}
}

.card-item {
	display: block;
	position: relative;
	border: 1px solid var(--color-gold);
	overflow: hidden;
	width: 100%;
	height: 0;
	padding-bottom: 132.5%;
	transition: border-color 0.2s linear;

	&::after {
		content: '';
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 2;
		width: 100%;
		height: 33%;
		background-image: linear-gradient(to top, var(--color-black), rgba(0, 0, 0, 0));
		opacity: 0.8;
	}

	picture {
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 1;
		width: 100%;
		height: 100%;
		transform: translate(-50%, -50%) translateZ(0);
		backface-visibility: hidden;
		transform-origin: left top;
		transition: transform 0.2s linear;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__body {
		position: absolute;
		bottom: get-rem(12);
		left: get-rem(12);
		z-index: 3;
		display: flex;
		flex-direction: column;
		gap: get-rem(6);
		width: calc(100% - #{get-rem(10)});

		@media(min-width: 1024px) {
			bottom: get-vw(22);
			left: get-vw(24);
		}

		@media(min-width: 1921px) {
			bottom: get-rem(22);
			left: get-rem(24);
		}
	}

	&:hover {
		border-color: rgba(#ffc37a, 0.1);

		picture {
			transform: scale(1.02) translate(-50%, -50%) translateZ(0);
		}
	}

	&--short {
		padding-bottom: 76.2%;
		min-height: get-rem(180);
	}

	&--no-border {
		border: none;
	}
}

.default-title {
	font-size: get-rem(35);
	text-transform: uppercase;
	color: var(--color-white);

	.card-item & {
		font-size: get-rem(18);

		@media(min-width: 460px) {
			font-size: get-rem(21);
		}

		@media(min-width: 1024px) {
			font-size: get-vw(35);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(35);
		}
	}
}

.default-sub {
	font-size: get-rem(25.5);
	text-transform: uppercase;
	color: rgba(#fff, 0.5);

	.card-item & {
		font-size: get-rem(14);

		@media(min-width: 460px) {
			font-size: get-rem(18);
		}

		@media(min-width: 1024px) {
			font-size: get-vw(25.5);
		}

		@media(min-width: 1921px) {
			font-size: get-rem(25.5);
		}
	}
}
