@use "../utils/_functions" as *;
@use "../utils/_vars" as *;

.gentlemen-club {
	background-size: cover;
	background-position: right -240px center;
	background-repeat: no-repeat;
	position: relative;
	padding: get-rem(40) 0;

	@media(min-width: 640px) {
		padding: get-rem(60) 0;
	}

	@media(min-width: 992px) {
		padding: get-vw(121) 0 get-vw(102);
		background-position: right center;
	}

	@media(min-width: 1921px) {
		padding: get-rem(121) 0 get-rem(102);
	}


	&::after {
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: 1;
		background-color: rgba(#000, 0.7);

		@media(min-width: 1320px) {
			background-color: rgba(#000, 0);
		}
	}

	.simple-text {
		@media(min-width: 992px) and (max-width: 1920px) {
		    font-size: get-vw(40);
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		width: 100%;
		position: relative;
		z-index: 2;

		@media(min-width: 1320px) {
			max-width: get-vw(1000);
		}

		@media(min-width: 1921px) {
			max-width: get-rem(1000);
		}

		.title {
			margin: 0 0 get-rem(27);
		}

		.default-ul {
			margin: get-rem(16) 0 get-rem(22);
			padding: 0 0 0 get-rem(16);

			@media(min-width: 1320px) {
				margin: 0 0 get-rem(16);
				padding: 0 0 0 get-rem(29);
			}

			li {
				@media(min-width: 992px) and (max-width: 1920px) {
					font-size: get-vw(40);
				}
			}
		}

		.button {
			width: 100%;

			@media(min-width: 560px) {
				max-width: get-rem(244);
			}
		}
	}
}
